import React from 'react';
import './index.css';
import Amico from '@components/assets/amico.png';
import Rafiki from '@components/assets/rafiki.png';
import ContentBox from './components/ContentBox';
import { Link } from 'react-router-dom';

export default function Body() {
    return (
        <div className="homepage_content_info container">
            <div className="row">
                <div className="col-md-6 desc_wrapper">
                    <div className="desc_container">
                        <div className="module_text">Are you a company?</div>
                        <div className="desc_text">
                            In a world of distrust, build trust and credibility with sojiare
                        </div>
                        <Link to="/company/signup" className="desc_button">
                            Get Started
                        </Link>
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="content_img" style={{ backgroundImage: `url(${Amico})` }}></div>
                </div>
            </div>

            {/* Get Started */}
            <div className="homepage_content_info2 row">
                <div className="col-md-6 content_img_container">
                    <div className="content_img" style={{ backgroundImage: `url(${Rafiki})` }}></div>
                </div>
                <div className="col-md-6">
                    <div className="desc2_title">Get Started</div>
                    <div className="row">
                        <div className="col-md-6">
                            <ContentBox
                                number="1"
                                title="Sign Up"
                                desc="Register your company to build trust and credibility from customers and potential investors"
                            />
                        </div>
                        <div className="col-md-6">
                            <ContentBox
                                number="2"
                                title="Validate Content"
                                desc="Post link on social media pages bio for easy access and verification. Encourage existing customers to review and post comments about your company."
                            />
                        </div>
                        <div className="col-md-6">
                            <ContentBox
                                number="3"
                                title="Protect Your Customers"
                                desc="Authenticate your company, business and organisation to get the trust of your customers"
                            />
                        </div>
                        <div className="col-md-6">
                            <ContentBox
                                number="4"
                                title="Track Feedback"
                                desc="Enjoy premium access by tracking feedback of comments and reviews posted about your company"
                            />
                        </div>
                    </div>
                    <div className="desc2_button_container">
                        <Link to="/company/signup" className="desc_button">
                            Get Started
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
}
